import {Component} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {AuthentificationService} from '../../services/authentification';
@Component({
  selector: 'app-login',
  templateUrl: 'login.html',
  styleUrls: ['login.scss'],
  providers: [AuthentificationService]
})
export class LoginComponent {
  loginForm: FormGroup;

  constructor(public formBuilder: FormBuilder, public authentificationService: AuthentificationService) {
    this.loginForm = formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
    });
  }
  login() {
    console.log(this.loginForm['_value']);
    const creds = [{'username': this.loginForm['username'], 'password' : this.loginForm['password']}];
    this.authentificationService.loginService(JSON.stringify(creds)).then(data => {
      console.log(data);
    });
  }
}



